@import "init";

body{
  font-size: 18px;
}

// Blog
#block-views-block-blog-latest-posts-block-1 {
  > h2 {
    display: none;
  }
  .more-link {
    text-align: right;
    display: block;
    margin: 0 auto;
    max-width: 300px;
  }
}

#block-views-block-blog-view-a-topic-block-1 {
  display: none;
}

// About
.profile {
  &.compact {
    margin: 0 auto;
  }
}

// Contact
span.tel {
  a {
    color: $body_color;
    text-decoration: none;
  }
}
#pane-contact {
  h4 {
    hr {
      width: 100%;
      left: 0;
      &.hr-right {
        display: none;
      }
    }
    span {
      background: $body_bg_color;
    }
  }
}


.page-header span#active-indicator {
  border-bottom: 0;
}

header {
  ul.menu {
    display: none;
  }
}
#title {
  font-family: $pathway_font_stack;
  font-weight: normal;
  font-size: 35px;
  text-transform: uppercase;
}
.mobile-logo {
  position: absolute;
  right: 0;
  width: 70px;
  height: 70px;
  @include background-image-retina('#{$img_path}/nav-mobile_logo', 'png', 70px, 70px);
}

.menu-title {
  width: 60px;
  text-align: center;
  font-size: 20px;
  background: $body_color;
  color: #fff;
  font-size: 15px;
  cursor: pointer;
  text-decoration: none;
  i {
    font-size: 20px;
    display: block;
    clear: both;
  }
}

#navigation {
  font-family: $pathway_font_stack;
  font-weight: normal;
  color: #fff;
  text-transform: uppercase;
}
#navigation ul.mobile-menu {
  max-height: 0;
  background: $body_color;
  transition: all 0.5s ease;
  li {
    border-top: 1px solid #fff;
    border-right: 1px solid #fff;
    border-left: 1px solid #fff;
    a {
      font-size: 22px;
      border: 1px solid #fff;
      color: #fff;
      text-decoration: none;
    }
  }
}
#navigation .show-menu {
  + ul.mobile-menu {
    max-height: 290px;
    border-bottom: 1px solid #fff;
  }
}

//Work
#pane-work {

}

// Home
#pane-home {
  margin-bottom: 50px;
  body.path-frontpage & {
    @include background-image-retina('#{$img_path}/home-mobile_train', 'png', 1352px, 163px);
    background-position: bottom;
    background-repeat: no-repeat;
    padding-top: 90px;
    padding-bottom: 260px;
  }
  h2.slogan {
    display: block;
    font-size: 18px;
    margin: 0 auto;
    &.top-line {
      margin-bottom: 20px;
    }
    .small {

    }
    .mobile-wide {
      font-weight: 900;
    }
    .thin{
      color: $body_color;
    }
    .first-line{
      padding: 20px 50px 10px 50px;
      background: url("#{$img_path}/red-bar-200px-wide.png") no-repeat scroll 28px 14px;
    }
  }
  .home-pane-mobile-logo {
    display: block;
    width: 157px;
    height: 167px;
    margin: 30px auto;
    @include background-image-retina('#{$img_path}/home-mobile_logo', 'png', 157px, 167px);
  }
  .media-links {
    margin-top: 30px;
    margin-right: auto;
    margin-left: auto;
    max-width: 200px;
    a {
      margin: 10px;
    }
  }
  a.scroll {
    display: none;
  }
}

.node--type-portfolio {
  &.node--view-mode-teaser {
    img {
      box-shadow: 0 0 10px #818080;
    }
    h2 {
      font-family: $pathway_font_stack;
      font-weight: normal;
      font-size: 25px;
      float: left;
      a {
        text-decoration: none;
        color: #3f444f;
      }
    }
    .field.field--name-title {
      max-width: 185px;
      float:left;
      max-height: 1em;
      overflow: hidden;
    }
    .field.field--name-field-cover-image img {
      box-shadow: 0px 2px 10px;
    }
    .portfolio-button {
      float: right;
    }
  }
  &.node--view-mode-full {
    .cycle-slide {
      img {
        box-shadow: 0 2px 8px 2px #000;
      }
    }
    .cycle-pager {
      img {
        box-shadow: 0 0 5px #000;
      }
    }
  }
  h2 {
    height: auto;
    overflow: visible;
  }
}

.portfolio-button{
  background: $body_color;
  color: #fff;
  text-decoration: none;
  font-family: $pathway_font_stack;
  font-weight: normal;
  text-transform: uppercase;
}

#block-portfoliologos {
  position: relative;
  z-index: 0;
  h2 {
    font-family: $pathway_font_stack;
    font-weight: normal;
    margin: 10px 0;
    font-size: 36px;
    text-transform: uppercase;
  }
  #container {
    display: block;
    overflow: hidden;
  }
  .cycle-slideshow {
    display: block;
    margin-top: 25px;
    margin-right: auto;
    margin-left: auto;
    max-width: 160px;
    &:first-child {
      margin-top: 0;
    }
    img {
      max-height: 160px;
      max-width: 160px;
    }
  }
}

.scrollcontrol-focus-pane-work {
  #block-views-block-portfolio-block-1 div.item-list {
    position: fixed !important;
    width: 100%;
    top: 73px;
    left: 0;
    right: 0;
    background: $body_bg_color;
    z-index: 25;
    a {
      color: $body_color;
    }
  }
}

div.item-list.fixed-pager {
  height: 36px;
  ul.pager {
    position: fixed;
    top: 80px;
    left: 0;
    right: 0;
    z-index: 2;
    padding: 0 20px;
    height: 44px;
    background: $body_color;
    animation: fixPager 0.3s ease-out;
    max-width: 100% !important;
    a {
      color: #fff !important;
    }
  }
}

// TODO: better way to handle this?
.touch div.item-list.fixed-pager ul.pager a:after {
  padding: 0 !important;
  height: 0 !important;
}

.exception {
  #pane-home {
    text-align: center;
  }
  .layout-content {
    text-align: center;
    background: rgba(245, 245, 245, .75);
  }
  height: 100%;
  @include background-image-retina('#{$img_path}/train-stop', 'png', 1911px, 405px);
  background-position: center 100%;
  background-repeat: no-repeat;
}
